<template>
    <!-- 整体背景 -->
    <div style="background-color:#EEEEEE;">
        <section class="page-header"  :style="'background-image: linear-gradient(120deg, '+backgroundColorLeft+', '+backgroundColorRight+');color: '+fontColor+';'">
            <div style="position:absolute; top:20px; right:20px; z-index:2;" v-show="false">
                <el-tooltip effect="dark" :content="fullButton.full?'退出':'全屏'" placement="bottom-end">
                    <el-button @click="full" :icon="fullButton.full?'el-icon-close':'el-icon-rank'" circle ></el-button>
                </el-tooltip>
            </div>
            <div v-show="false" v-for="(item,index) in randomIcon" :key="'ri'+index" :style="'position:absolute; top:'+item.top+'px; left:'+item.left+'px; z-index:1;'">
                <font :style="'font-size: '+item.size+'px;color:#fff;'">
                    <b>♪</b>
                </font>
            </div>
            <h3 class="project-name" style=" text-align:left;" v-if="false">{{blogTitle}}</h3>      
            <h3 class="project-tagline" v-if="false">{{blogDescribe}}</h3>
            <a :href="'https://github.com/'+githubUsername" class="btn" v-if="false" target="_blank">GitHub主页</a>
            <a :href="'https://github.com/'+githubUsername+'VBlog'" class="btn" target="_blank" v-if="!mini" style="display: none;">博客源码</a>
            <div  style="position:relative;  z-index:2;margin: auto;width:64rem; margin-bottom: 0px; background-color: #5D7DB3; border: 1px solid #5D7DB3;">
            <el-card shadow="never" :body-style="{ padding: '0px'}">
                <el-row style="background-color: #5D7DB3;">
                    <el-col  :span="4" style="text-align: center;padding: 15px 0px 0px 0px">
                        <h3 class="project-name" style=" text-align:left; margin-left: 0px;color:white;">{{blogTitle}}</h3>      
                    </el-col>                                
                </el-row>
                <el-row style="background-color: #5D7DB3; margin: 0px 0px 0px -18px">
                    <el-col :span="2" style="text-align: center;">
                        <img  @click="goToGithub" v-popover:bigAvatar :src="avatarUrl" style="width:48px; height:48px; border-radius:24px; 
                        border: 1px solid #5D7DB3" />
                        <el-popover ref="bigAvatar" placement="top-start" :title="githubUsername" width="200" trigger="hover">
                            <i class="el-icon-star-on"></i>&emsp;{{name}}
                            <br>
                            <i class="el-icon-location"></i>&emsp;{{location}}
                            <br>
                            <img :src="avatarUrl" style="width: 200px;height: 200px;">
                        </el-popover>
                    </el-col>

                    <el-col :span="2" style="text-align: center; margin-left: -6px;">
                        <el-button @click="goToBlogGarden"  style="width:48px; height:48px; display: flex; justify-content: center; align-items: center;font-weight: bold; color:#5D7DB3;background-color:#BDC7E3;border: 1px solid #BDC7E3;" circle >博客园</el-button>
                    </el-col>
                    
                    <el-col :span="2" style="text-align: center;margin-left: -25px;">
                        <el-button @click="goToGitee"  style="width:48px; height:48px; display: flex; justify-content: center; align-items: center; font-weight: bold ;color:#5D7DB3;background-color:#BDC7E3;border: 1px solid #BDC7E3;" circle >gitee</el-button>
                    </el-col>

                    <el-col v-if="false" :span="10" style="background-color: #5D7DB3;">
                        <el-menu @select="selectTopbar" :default-active="topbar.active" style="background-color: #5D7DB3;" mode="horizontal" menu-trigger="click">
                            <el-submenu index="#more">
                                <template slot="title">了解博主</template>
                                <el-menu-item index="#githubHome">github主页</el-menu-item>
                                <el-menu-item index="#blog">其他博客</el-menu-item>
                            </el-submenu>
                            <el-submenu index="#webSites" v-if="webSites.length>0">
                                <template slot="title">其他网站</template>
                                <el-menu-item :index="'#webSites-'+index" v-for="(item,index) in webSites" :key="'#webSites'+index">{{item.name}}</el-menu-item>
                            </el-submenu>
                        </el-menu>
                    </el-col>
                  
                    <!-- <el-col :span="4" style="text-align: right;">
                        <div style="font-size: 20px;color:#606266;margin-top: 5px">
                            <b>{{githubUsername}}</b>
                        </div>
                        <div style="color:#606266;">
                            <i class="el-icon-location"></i>&nbsp;{{location?location:'未填写地址'}}
                            <br>
                        </div>
                    </el-col> -->               
                </el-row>
            </el-card>
        </div>
        </section>     
        <section class="main-content" >
            <el-row >
                <el-col :span="6" style="padding-right:0px;">
                    <sidebar></sidebar>
                </el-col>
                <el-col :span="18" style="padding-left:10px">
                    <app-main></app-main>
                </el-col>
            </el-row>
        </section>
        <section class="foot"  >
            <foot></foot>
        </section>
    </div>
</template>
<script>
    
import { mapGetters } from 'vuex';
import AppMain from './components/AppMain';
import Foot from './components/Foot';
import Sidebar from './components/Sidebar';
    export default {
        components: {
            Sidebar,
            AppMain,
            Foot
        },
        data() {
            return {
                music: {
                    isPlay: false,
                    currentTime: 0,
                    maxTime: 0,
                    volume: 100
                },
                fullButton: {
                    full: false
                },
                topbar: {
                    active: "",
                },
                randomIcon: []
            }
        },
        computed: {
            ...mapGetters([
                //githubUsername 是通过 Vuex 的 mapGetters 从 Vuex store 中获取的。
                //这意味着 githubUsername 是一个存储在 Vuex 中的状态属性
                'githubUsername',
                'blogTitle',
                'blogDescribe',
                'avatarUrl',
                'name',
                'location',
                'blog',
                'fontColor',
                'useBackgroundImage',
                'backgroundColorLeft',
                'backgroundColorRight',
                'audioUrl',
                'mini',
                'followersTotal',
                'followingTotal',
                'webSites'
            ])
        },
        watch: {
            '$refs.music.currentTime': function () {
                console.log(this.$refs.music.currentTime)
            }
        },
        mounted() {       
            let width = window.innerWidth
            for (let i = 0; i < 12; i++) {
                let temp = {}
                let left = this.$util.randomInt(10, width - 310)
                if(left>width/2-150){
                    left+=300
                }
                temp["left"] = left
                temp["top"] = this.$util.randomInt(20, 300)
                temp["size"] = this.$util.randomInt(20, 40)
                this.randomIcon.push(temp)
            }
        },
        created() {

        },
        methods: {
            goToGithub() {
                 window.open('https://github.com/' + this.githubUsername);
            },

            //博客园
            goToBlogGarden() {
                if (this.blog) {
                            window.open((this.blog.match(/https?:\/\//i)?'':'https://') + this.blog)
                        } else {
                            this.$message({
                                message: '博主没有其他博客',
                                type: 'info'
                            })
                        }
            },

            goToGitee() {
                let url = "https://gitee.com/tanzhiweiwxy/projects"
                window.open((url.match(/https?:\/\//i)?'':'https://') + url)
            },

            selectTopbar(index) {
                //取消菜单选中状态
                this.topbar.active = this.topbar.active == "" ? " " : ""
                switch (index) {
                    case "#githubHome":
                        //在新的窗口打开链接
                        window.open('https://github.com/' + this.githubUsername)
                        break
                    case "#blog":
                        if (this.blog) {
                            window.open((this.blog.match(/https?:\/\//i)?'':'https://') + this.blog)
                        } else {
                            this.$message({
                                message: '博主没有其他博客',
                                type: 'info'
                            })
                        }
                        break
                    default:
                        if(/#webSites-\d+/.test(index)){
                            let i = parseInt(index.split('-')[1])
                            let url = this.webSites[i].url
                            window.open((url.match(/https?:\/\//i)?'':'https://') + url)
                        }
                        break
                }
            },
            moveIcon(index) {
                let width = window.innerWidth
                this.randomIcon[index]["top"] = this.$util.randomInt(20, 300)
                let left = this.$util.randomInt(10, width - 310)
                if(left>width/2-150){
                    left+=300
                }
                this.randomIcon[index]["left"] = left
            },
            full() {
                if (!this.fullButton.full) {
                    this.$util.fullScreen()
                    this.fullButton.full = true
                } else {
                    this.$util.fullExit()
                    this.fullButton.full = false
                }
            },           
        }
    }
</script>

<style>
    .page-header {
        padding: 1rem 4rem 1rem 4rem;
        color: #fff;
        text-align: center;
        background-color: #159957;
        background-image: linear-gradient(120deg, #155799, #159957);
        height: 120px
    }

    .project-name {
        font-size: 3rem;
        margin-top: 0;
        margin-bottom: 0rem;
    }

    .project-tagline {
        font-size: 1.25rem;
        margin-bottom: 2rem;
        font-weight: normal;
        opacity: 0.7;
    }

    .btn:hover {
        color: rgba(255, 255, 255, 0.8);
        text-decoration: none;
        background-color: rgba(255, 255, 255, 0.2);
        border-color: rgba(255, 255, 255, 0.3);
    }

    a:hover {
        text-decoration: underline;
    }

    a:active,
    a:hover {
        outline: 0;
    }

    .btn {
        padding: 0.75rem 1rem;
        display: inline-block;
        margin-bottom: 1rem;
        color: rgba(255, 255, 255, 0.7);
        background-color: rgba(255, 255, 255, 0.08);
        border-color: rgba(255, 255, 255, 0.2);
        border-style: solid;
        border-width: 1px;
        border-radius: 0.3rem;
        transition: color 0.2s, background-color 0.2s, border-color 0.2s;
    }

    a {
        color: #1e6bb8;
        text-decoration: none;
    }

    .btn+.btn {
        margin-left: 1rem;
    }

    .main-content {
        max-width: 64rem;
        padding: 10px 0px 5px 0px;
        margin: 0 auto;
        font-size: 1.1rem;
        word-wrap: break-word;
        min-height: 750px;
    }

    .foot {
        max-width: 64rem;
        margin: 0 auto;
        font-size: 12px !important;
        color: #586069 !important;
        word-wrap: break-word;
    }

    .el-card {
    border-radius: 0px !important;
    border: 0px solid #5D7DB3 !important;
    background-color: #FFF;
    overflow: hidden;
    color: #303133;
    -webkit-transition: .3s;
    transition: .3s;
    }
</style>


