<template>
	<div style="border-top: 1px #e1e4e8 solid !important;padding: 5px 0px 5px 0px;">
		<el-row>
            <el-col :span="3" style="text-align: center;padding: 15px 0px 0px 0px">
                <div >©2024 terry</div>
            </el-col>

			<el-col :span="8" style="text-align: center;padding: 5px 0px 0px 0px">            
                        <el-row>
                            <el-col :span="4">
                                <el-popover placement="top" trigger="hover">
                                    <div style="text-align: center">
                                        <el-progress color="#67C23A" type="circle" :percentage="music.volume"></el-progress>
                                        <br>
                                        <el-button @click="changeVolume(-10)" icon="el-icon-minus" circle></el-button>
                                        <el-button @click="changeVolume(10)" icon="el-icon-plus" circle></el-button>
                                    </div>
                                    <el-button @click="play" id="play" slot="reference" :icon="music.isPlay?'el-icon-refresh':'el-icon-caret-right'" circle></el-button>
                                </el-popover>
                            </el-col>
                            <el-col :span="14" style="padding-left: 10px">
                                <el-slider @change="changeTime" :format-tooltip="$util.formatTime" :max="music.maxTime" v-model="music.currentTime" style="width: 100%;"></el-slider>
                            </el-col>
                            <el-col :span="6" style="padding: 9px 0px 0px 5px;color:#909399;font-size: 13px">
                                {{$util.formatTime(music.currentTime)}}/{{$util.formatTime(music.maxTime)}}
                            </el-col>
                        </el-row>

                        <audio ref="music" loop autoplay v-if="audioAutoPlay">
                            <source :src="audioUrl" type="audio/mpeg">
                        </audio>
                        <audio ref="music" loop v-else>
                            <source :src="audioUrl" type="audio/mpeg">
                        </audio>
             </el-col>
			<el-col :span="10" v-show="false" >
				<div >
					©2024 terry117&emsp;&emsp;
					<a href="https://github.com/terry117" v-show="false" target="_blank">Profile</a>&emsp;&emsp;
					<a v-show="false" href="https://github.com/terry117/PersonalWebsite" target="_blank">VBlog</a>
				</div>
			</el-col>
			<el-col :span="4" v-show="false">
				<div style="text-align: center;font-size: 18px">
					<i class="el-icon-location-outline"></i>
				</div>
			</el-col>
			<el-col :span="10" v-show="false">
				<div style="float: right;">
					<a href="https://developer.github.com" target="_blank">GitHub-API</a>&emsp;&emsp;
					<a href="https://cn.vuejs.org/" target="_blank">Vue.js</a>&emsp;&emsp;
					<a href="http://element.eleme.io/" target="_blank">Element</a>
				</div>
			</el-col>
		</el-row>
	</div>
</template>
<script>

import { mapGetters } from 'vuex';
	export default {
		data() {
			return {
				music: {
                    isPlay: false,
                    currentTime: 0,
                    maxTime: 0,
                    volume: 100
                },
			}
		},
		computed: {
            ...mapGetters([
                'audioUrl',
                'audioAutoPlay',
            ])
        },
		watch: {
            '$refs.music.currentTime': function () {
                console.log(this.$refs.music.currentTime)
            }
        },
		mounted() {
            this.$nextTick(() => {
                setInterval(this.listenMusic, 1000)
            })     
        },
		methods: {
			listenMusic() {
                if (!this.$refs.music) {
                    return
                }
                if (this.$refs.music.readyState) {
                    this.music.maxTime = this.$refs.music.duration
                }
                this.music.isPlay = !this.$refs.music.paused
                this.music.currentTime = this.$refs.music.currentTime
            },
            play() {
                if (this.$refs.music.paused) {
                    this.$refs.music.play()
                } else {
                    this.$refs.music.pause()
                }
                this.music.isPlay = !this.$refs.music.paused
                this.$nextTick(() => {
                    document.getElementById('play').blur()
                })

            },
            changeTime(time) {
                this.$refs.music.currentTime = time
            },
            changeVolume(v) {
                this.music.volume += v
                if (this.music.volume > 100) {
                    this.music.volume = 100
                }
                if (this.music.volume < 0) {
                    this.music.volume = 0
                }
                this.$refs.music.volume = this.music.volume / 100
            }
		}
	}
</script>


