<template>
    <div style="background-color:#D3D3D3; border-color: #D3D3D3; " >
        <el-card shadow="never" style="background-color:#D3D3D3 ;border-color: #D3D3D3;" >
            <el-menu :default-active="active" @select="onSelect" background-color="#5D7DB3"  active-text-color="#0000CD"  text-color="#fff"  style="margin: -20px;">
                <el-menu-item v-for="item in constantRouterMap" v-if="item.meta&& item.meta.type=='user'&&(blogToken||!item.meta.LoginRequired)&&(!mini||!item.meta.mini)"
                    :key="item.path" :index="item.path "  style="margin-top: 10px;" >
                    <i :class="item.meta.icon" ></i>
                    <span slot="title" style="font-size: large;">{{item.meta.title}}</span>
                </el-menu-item>
            </el-menu>
        </el-card >

        <el-card v-show="false" shadow="never" style="margin-top: 20px;text-align: center">
            <div v-if="!token" style="font-size: 0.9rem;line-height: 1.5;color: #606c71;">
                <el-tag type="danger" size="small">&nbsp;</el-tag>&nbsp;&nbsp; Token未绑定&nbsp;&nbsp;
                <el-button type="text" @click="openTokenDialog">绑定</el-button>
            </div>
            <div v-if="token" style="font-size: 0.9rem;line-height: 1.5;color: #303133;">
                <el-tag type="success" size="small">&nbsp;</el-tag>&nbsp;&nbsp; Token已绑定&nbsp;&nbsp;
                <el-button type="text" @click="cancellation">注销</el-button>
            </div>
            <div style="margin-top: 10px;text-align: left" v-show="false">
                <el-alert title="Token获取" type="info" description="在 github-> settings-> developerSettings-> personalAccessTokens 勾选gist权限,获取Token."
                    :closable="false">
                </el-alert>
            </div>
        </el-card>
        <token-dialog ref="tokenDialog"></token-dialog>
    </div>
</template>

<script>
    import { constantRouterMap } from '@/router';
import TokenDialog from '@/views/common/TokenDialog';
import { mapGetters } from 'vuex';
    export default {
        components: {
            TokenDialog
        },
        data() {
            return {
                constantRouterMap,
                active: "",
                parentUrl: "",
                menuList: []
            }
        },
        computed: {
            ...mapGetters([
                'token',
                'githubUsername',
                'mini',
                'blogToken'
            ])
        },
        mounted() {
            let arr = this.$route.path.split("/")
            this.active = "/" + arr[1] + "/" + arr[2]
        },
        methods: {
            onSelect(index) {
                this.$router.push(index)
            },
            openTokenDialog() {
                this.$refs.tokenDialog.open(() => {         
                })
            },
            cancellation() {
                this.$store.dispatch("Cancellation")
            }
        }
    }
</script>

<style scoped>
.el-menu-item.is-active i {
    color: inherit;
}

.el-menu-item i {
    color: #ffffff;
}
</style>





